.body__start-animation {
  background: linear-gradient(to left, #1669bc, #170099);
  animation: startAnimationBody 20s infinite;
}

@keyframes startAnimationBody {
  0% {
    background: linear-gradient(to left, #1669bc, #170099);
  }

  25% {
    background: linear-gradient(to left, #ff5e00, #a53400);
  }

  50% {
    background: linear-gradient(to left, #1e90ff, #00ced1);
  }

  75% {
    background: linear-gradient(to left, #227e8e, #0f931e);
  }

  100% {
    background: linear-gradient(to left, #1669bc, #170099);
  }
}

.wrapper__start-animation {
  transform-origin: 0 0;
  animation: startAnimationWrapper 2s ease-in-out;
}

@keyframes startAnimationWrapper {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  60% {
    opacity: .1;
    transform: scale(1.02);
  }
}

.header__person-animation {
  animation: moveTitle 60s infinite;
}

@keyframes moveTitle {
  0%, 4% {
    transform: scale(1);
  }

  2% {
    transform: scale(1.05);
  }

  23%, 28.5%, 48%, 51% {
    transform: scale(1) rotate(0);
  }

  26%, 28% {
    transform: scaleX(.4) rotate(0);
    transform-origin: 40%;
  }

  28.3% {
    transform: scaleX(1.15);
  }

  50% {
    transform: scale(1)rotate(-3deg);
  }

  50.5% {
    transform: scale(1)rotate(1deg);
  }

  79%, 81% {
    transform: scale(1)translateX(0);
  }

  80% {
    transform: translateX(-30px);
  }
}

.header__letter-animation {
  transform-origin: center;
  animation: rotateLetter 10s infinite;
  display: inline-block;
}

@keyframes rotateLetter {
  0%, 10%, 100% {
    transform: rotate(0);
  }

  5% {
    transform: rotateX(180deg);
  }
}

.modal__window-animation {
  animation: windowOpacity 1s ease-in-out;
}

@keyframes windowOpacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.gallery__img-animation {
  transform-origin: top;
  animation: imgAnimationGallery 1s ease-in-out;
}

@keyframes imgAnimationGallery {
  0% {
    transform: scaleY(0);
  }
}

